
































































































































































































































































































































































#layout {
  width: 100%;
  min-height: 100%;
}
#layoutDesk {
  display: grid;
  grid: minmax(100vh, auto) / min-content;
  min-height: 100vh;
}
.gxStickyHeight {
  height: 100vh;
}
.gxDesktopTopNav {
  grid-area: 1 / 3 / -1 / 1;
}
.gxDeAside {
  grid-area: 1 / 1 / -1 / -1;
  // height: 100vh;
}
.gxDeMain {
  grid-area: 1/ 2 / -1 / -1;
}
.gx-padding-menu {
  padding-bottom: 6.375rem;
}
.gxDeStickyContainer {
  top: 0;
}
// top-bar styles

.custom-slot-enter-active,
.custom-slot-leave-active,
.input-fade-enter-active {
  transition: opacity 0.2s ease-out;
}
.input-fade-leave-active {
  transition: opacity 0.2s ease-in;
}
.input-fade-enter,
.custom-slot-enter,
.custom-slot-leave-to,
.input-fade-leave-to {
  opacity: 0;
}
.top-bar {
  transition: background 0.3s ease-out;
}
.icon-button {
  font-size: 1.25rem;
}
.gxInput {
  font-size: 1.125rem;
  line-height: 1.125rem;
}
